import opg from 'ts/opg.ts';
import PopUp from 'ts/ui/Popup';
import {Languages} from 'ts/util/Languages';
import {Combo} from 'ts/ui/Combo';
import {url} from "ts/util/utils";
import {Cache} from 'ts/util/store';

const lpg = Languages.package;
console.log('Languages:', Languages);

const cache = Cache.getInstance();
const row = cache.get('row');

console.log(row);

opg.api({
	'addMain!post': 'Information/AddInfomationSubject',
	'updateMain!post': 'Information/UpInfomationSubject',
	getSubjects: 'Information/InformationById',
	'upload!UPLOAD': 'upload/files',
});

let newsId ;
const subjectPage = __uri('/pages/content/news/addSubject.html');


jQuery.datetimepicker.setLocale(Languages.current);
$('#beginDate,#endDate').each(function (i, elem) {
	$(elem).datetimepicker({
		//timepicker: false,
		//closeOnDateSelect: true,
		format: 'Y-m-d H:i:00',
	});
});


let iptCoverPicture = $('#coverPicture');
let tdCover = $('#tdCover');
Combo.makeClearableInput(iptCoverPicture, $({}));
$('.ipt-eraser' , '#tdUpload').on('click' , function () {
	//if(!this.value){
		tdCover.empty();
	//}
});

let uploadForm = $(`<form>
						<input name="file" type="file" accept="image/jpg,image/jpeg,image/png">
				</form>`);
$('#btnUploadDialog').click(function () {
	let pop: PopUp = top.opg.confirm(uploadForm, function () {
		let formData = new FormData(uploadForm[0] as HTMLFormElement);
		opg.api.upload(formData, function (data) {
			iptCoverPicture.val(data[0].downloadLink);
			tdCover.html(`<img src="${data[0].downloadLink}" style="max-height: 100px;"/>`);
			pop.close();
		});
		return true;
	}, {
		title: `${lpg.upload}`,
		width: 200,
		height: 50,
		buttons: {
			ok: `${lpg.upload}`,
			cancel: `${lpg.cancel}`,
		},
	});


});


let tbMain = $('#tbMain');

let btnAddSubject = $('#btnAddSubject');

if (row) {
	newsId = row.id;
	tbMain.jsonToFields(row);
	btnAddSubject.show();

	listSubjects();
}

function listSubjects(){
	opg.api.getSubjects({informationId: newsId}, (data) => {
		ulSubjects.bindList({
			list: data ,
			template: '<li><button class="btn-mini btn-info btnEditSub" data-id="${id}">'+lpg.edit+'</button> <button class="btn-mini btn-danger btnDelSub" data-id="${id}">'+lpg.del+'</button><hr>${content}</li>'
		})
	})
}


let ulSubjects = $('#ulSubjects');


//edit subjects
ulSubjects.on('click' , '.btnEditSub' , function () {
	let btn = $(this) , subId = btn.data('id');
	let src = url.setParam(subjectPage , {id:newsId , subId});
	let pop: PopUp = top.opg.confirm(`<iframe src="${src}" />`, function (i, ifr) {
		ifr.doSave(pop, listSubjects);
		return true;
	}, {
		title: `${lpg.editSubject}`,
		width: 500,
		height: 450,
		buttons: {
			ok: `${lpg.save}`,
			cancel: `${lpg.cancel}`,
		},
	});
});



$('#btnSaveMain').click(function () {
	let param = tbMain.fieldsToJson({
		title: {
			name: Languages.package.title,
			type: 'ns',
			require: true,
		},
	});

	if (param) {
		param.informationType = 0;
		param.enableStick = param.enableStick ? 1 : 0;

		if(newsId){
			param.id = newsId;
			param.stick = param.enableStick;
			opg.api.updateMain(param , (data)=>{

			});
		}
		else{
			opg.api.addMain(param,  (data) =>{
				newsId = data;

				//$('input,button', tbMain).prop('disabled', true);


				btnAddSubject.show();


			});
		}


	}
});


btnAddSubject.click(function () {
	let src = url.setParam(subjectPage , {id:newsId});
	let pop: PopUp = top.opg.confirm(`<iframe src="${src}" />`, function (i, ifr) {
		ifr.doSave(pop, listSubjects);
		return true;
	}, {
		title: `${lpg.addSubject}`,
		width: 500,
		height: 450,
		buttons: {
			ok: `${lpg.save}`,
			cancel: `${lpg.cancel}`,
		},
	});
});